html,
body {
  width: 100%;
  height: 100%;
}
/*属性变量*/
.warning-container {
  width: 100%;
  height: 100%;
  background: #1B418D;
  margin-top: 0.6rem;
  padding: 0.36rem 0.6rem 0;
}
.warning-container .warning-warp {
  width: 100%;
  height: 100%;
  flex-direction: row ;
}
.warning-container .warning-warp .left-warp,
.warning-container .warning-warp .right-warp {
  width: 508px;
}
.warning-container .warning-warp .center-warp {
  flex: 1;
}
